<template>
  <v-app v-bind:class="isDetail">
    <guide axisV v-if="!$store.state.isMobile"></guide>
    <!--<guide axisH v-else></guide>-->
    <header>
      <v-navigation-drawer
        dark
        app
        disable-resize-watcher
        fixed
        :clipped="$vuetify.breakpoint.lgAndUp"
        v-model="sidebar"
        right
        flat
        overflow
      >
        <keep-alive>
          <menu-bar menuPosition="side"></menu-bar>
        </keep-alive>
      </v-navigation-drawer>
      <v-toolbar app :clipped-left="$vuetify.breakpoint.lgAndUp" height="56px">
        <v-toolbar-title>{{$store.state.appName}}</v-toolbar-title>
        <v-spacer></v-spacer>
        <keep-alive>
          <menu-bar
            v-bind:class="{ active: position }"
            class="hidden-sm-and-down"
            menuPosition="top"
            v-if="$route.meta.layout === 'default'"
          ></menu-bar>

          <v-btn fab round flat @click="fn_back" v-else>
            <v-icon class="clear">clear</v-icon>
          </v-btn>
        </keep-alive>
        <v-toolbar-side-icon
          class="sidebar hidden-md-and-up"
          @click.stop="sidebar = !sidebar"
          v-if="$route.meta.layout === 'default'"
        ></v-toolbar-side-icon>
      </v-toolbar>
    </header>

    <!-- <v-content>
      <transition v-bind:name="transitionName" appear>
        <keep-alive>
          <component :is="layout">
            <router-view></router-view>
          </component>
        </keep-alive>
      </transition>
    </v-content> -->

    <v-content>
      <keep-alive>
        <component :is="layout">
          <router-view></router-view>
        </component>
      </keep-alive>
    </v-content>

    <keep-alive>
      <footer-bar></footer-bar>
    </keep-alive>
  </v-app>
</template>

<script>
import store from "./store";
import router from "./router";
import Guide from "@/components/Guide.vue";
import MenuBar from "@/components/Menus.vue";
import FooterBar from "@/components/Footer.vue";

const default_layout = "default";

export default {
  components: {
    Guide,
    MenuBar,
    FooterBar
  },
  data() {
    return {
      isMobile: false,
      isDetail: "default",
      sidebar: false,
      position: false,
      appName: "appName",
      transitionName: "zoom-in"
    };
  },
  beforeDestroy() {
    if (typeof window !== "undefined") {
      window.removeEventListener("resize", this.fn_resize, { passive: true });
    }
  },
  computed: {
    layout() {
      return (this.$route.meta.layout || default_layout) + "-layout";
    }
  },
  created() {
    this.appName = this.$store.state.appName;
    //console.log('app created', to.meta.layout)
  },
  mounted() {
    //console.log('app mounted')
    router.beforeEach((to, from, next) => {
      this.isDetail = to.meta.layout;
      //console.log('app mounted', to.meta.layout)
      if (to.meta.index > from.meta.index) {
        this.position = true;
      } else {
        this.position = false;
      }
      if (to.meta.transition) {
        this.transitionName = "zoom-in";
      } else {
        this.transitionName = "zoom-out";
      }
      next();
    });
    this.fn_resize();
    window.addEventListener("resize", this.fn_resize, { passive: true });
  },
  methods: {
    fn_resize() {
      store.commit("isMobile", { active: window.innerWidth < 750 });
    },
    fn_back: function() {
      router.go(-1);
    }
  }
};
</script>
